body {
  background-color: rgba(216, 147, 235, .635);
  margin: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.search-form {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #994caf;
  border: none;
  border-radius: 10px;
  padding: 10px;
  font-size: 16px;
}

input {
  max-width: 300px;
  border-radius: 10px;
  outline-color: #994caf;
  margin-right: 10px;
  padding-left: 10px;
  padding-right: 10px;
}

.load-more {
  justify-content: center;
  margin: 0 auto 30px;
  display: flex;
}

.is-hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.photo-card {
  height: 250px;
  width: calc(25% - 15px);
  border-radius: 10px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  transition: transform .3s ease-in-out;
  display: block;
}

.photo-card:hover img {
  transform: scale(1.1);
}

.info {
  color: #000;
  background-color: #fff;
  justify-content: space-evenly;
  padding: 10px;
  font-size: 14px;
  display: flex;
}

.info-item {
  text-align: center;
}

